<template>
    <div class="crewDigo">
        <el-dialog title="提示" :visible.sync="dialogVisible" width="65%" :before-close="handleClose" top="80vh">

            <div class="dialogCont" id="billDetail">
                <div class="comonTitle">基本信息</div>
                <div class="infoItem">
                    <div style="flex:2">井队名称：{{dialogBaseData.sdName}}</div>
                    <div>平台名称：{{dialogBaseData.kwell}}</div>
                    <div></div>
                </div>
                <div class="comonTitle">时间</div>
                <div class="infoItem">
                    <div>
                        起止时间：{{moment(dialogBaseData.arSt).format('YYYY-MM-DD')}} 至
                        {{moment(dialogBaseData.arEt).format('YYYY-MM-DD')}}
                    </div>
                </div>
                <div class="comonTitle">计量表信息</div>
                <div class="infoItem">
                    <div>起码：{{dialogBaseData.arSnum}}</div>
                    <div>止码： {{dialogBaseData.arEnum}}</div>
                    <div>倍率： {{dialogBaseData.blRate}}</div>
                    <div></div>
                </div>
                <div class="comonTitle">电量信息</div>
                <div class="infoItem">
                    <div>电量：{{dialogBaseData.useEq}}</div>
                    <div>线损电量： {{dialogBaseData.loseEq}}</div>
                    <div>补录电量： {{dialogBaseData.extraEq}}</div>
                    <div>总电量： {{dialogBaseData.allEq}}</div>
                </div>
                <div class="comonTitle">申请信息</div>
                <div class="infoItem">
                    <div>申请人： {{dialogBaseData.applyPerson}}</div>
                    <div>申请时间：
                        {{dialogBaseData.applyTime?moment(dialogBaseData.applyTime).format('YYYY-MM-DD HH:mm:ss'):'-' }}
                    </div>
                    <div>总电量： {{dialogBaseData.allEq}}</div>
                    <div></div>
                </div>
                <div class="comonTitle">详情</div>
                <div class="dialogTable">
                    <el-table :data="dialogTable" height="300"
                        :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
                        <el-table-column v-for="item in tableRows" :key="item.prop" :prop="item.prop"
                            :label="item.label">
                            <template slot-scope="scope">
                                <div v-if="item.prop==='day'">{{moment(scope.row[item.prop]).format('YYYY-MM-DD')}}
                                </div>
                                <div v-else>{{scope.row[item.prop]}}</div>
                            </template>
                        </el-table-column>
                    </el-table>

                </div>

            </div>
            <span slot="footer" class="dialog-footer">
                <div v-if="status==1">
                    <el-button @click="handleClose">取 消</el-button>
                </div>
                <div v-else>
                    <el-button type="primary" @click="subimt">确 认</el-button>
                </div>
            </span>

        </el-dialog>
    </div>

</template>

<script>
    import {
        formatDate
    } from '@/utils/index'
    import {
        billDetail,
        sureBill
    } from '@/api/bill'
    import {
        mapState
    } from 'vuex'
    import TempalteBill from "@/components/tempalteBill.vue"; //打印
    import moment from 'moment'
    export default {
        name: 'crewDialog',
        components: {
            TempalteBill,
        },
        props: {
            dialogVisible: {
                type: Boolean,
                default: false
            },
            dialogTable: {
                type: Array,
                default: () => []
            },
            dialogBaseData: {
                type: Object
            },
            id: {
                type: Number,
                default: '0',
            },
            status,

        },
        data() {
            return {
                moment,
                tableRows: [{
                        prop: 'day',
                        label: '日期'
                    },
                    {
                        prop: 'useEq',
                        label: '电量'
                    },
                    {
                        prop: 'remark',
                        label: '备注'
                    }
                ]
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId
            })
        },
        created() {
            this.tableD = this.dialogBaseData;
            // console.log(this.dialogBaseData)
        },

        mounted() {

        },
        methods: {
            handleClose() {
                this.$emit("cancel", false);
            },
            subimt() { //确认

                this.sureBill()
            },
            prints() { //打印
                // this.$message({
                //     message: '敬请期待！',
                //     type: 'warning'
                // })
                this.printExcel('billDetail')
            },
            sureBill() {
                const query = {
                    userId: this.userId,
                    id: this.id,
                }
                sureBill(query).then(res => {
                    this.$message({
                        message: '账单确认成功！',
                        type: 'success'
                    })
                    this.$emit("cancel", false);
                    this.$emit("getList")
                }).catch(err => {
                    this.$emit("cancel", false);
                })
            },
            printExcel(id) {
                // 空页面
                let printStr = "<html><head><meta http-equiv='Content-Type' content='text/html; charset=utf-8'></head>";
                // 定义element-ui table组件的样式
                const tabStyle = `<style>
                .comonTitle{color: #333;font-size: 0.14rem;font-weight: bold;}
                .infoItem{ display: flex;justify-content: space-between;margin-bottom: 0.1rem;margin-top: 0.1rem;}
                .infoItem div{ flex: 1;}
                .comonTitle{color: #333;font-size: 0.14rem;font-weight: bold;}
                table{width:100%;display:table-cell!important;box-sizing:border-box;}
                .el-table__header,.el-table__body,.el-table__footer{width:100%!important;border-collapse: collapse;text-align:center;}
                table,table tr th, table tr td { border:1px solid #ddd;color:#606266;word-wrap:break-word}
                table tr th,table tr td{padding:4mm 0mm;word-wrap:break-word }
                .el-table__body, tr td .cell{width:100%!important}
                .el-table th.gutter{display: none;}
                .el-table colgroup.gutter{display: none;}
                </style><body>`;
                let content = "";
                // 获取名为传入id的 dom元素内的内容
                let str = document.getElementById(id).innerHTML;
                // 拼接空页面+style样式+dom内容
                content = content + str;
                printStr = printStr + tabStyle + content + "</body></html>";
                // 打开新页面
                let pwin = window.open("_blank");
                // 将内容赋值到新页面
                pwin.document.write(printStr);
                pwin.document.close();
                // 聚焦-不加focuse，在某些情况下，打印页面会有问题。
                pwin.focus();
                // 使用setTimeout，等页面dom元素渲染完成后再打印。
                setTimeout(() => {
                    pwin.print(); // 打印功能。 例如 window.print() 直接打印当前整个页面。 
                    pwin.close(); // 关闭 打印创建的当前页面
                }, 500);

            }


        }
    }
</script>

