import {
    request
} from '../utils/request'
export function getBills(query) { //登录
    return request({
        method: 'get',
        url: `dl/bill/`,
        params: query
    })
}
export function billDetail(id) { //账单明细
    return request({
        method: 'get',
        url: `dl/bill/get/` + id,
    })
}

export function sureBill(data) { //确认
    return request({
        method: 'post',
        url: `dl/bill/confirm`,
        data
    })
}



export function innerBills(query) { //公司下计量点账单查询
    return request({
        method: 'get',
        url: `dl/bill/comfirm/list/innerBills`,
        params: query
    })
}
