<template>
    <div class="home commpage">
        <div class="crew">
            <div class="crew-head common-pfc">
                <el-tabs v-model="queryFrom.status" @tab-click="tabClick">
                    <el-tab-pane label="待确认电量账单" name="0">
                    </el-tab-pane>
                    <el-tab-pane label="历史确认账单" name="1">
                    </el-tab-pane>

                </el-tabs>
            </div>
            <div class="crew-cont">
                <div class="crew-table">
                    <el-table :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
                        <el-table-column type="index" label="序号" width="200px" />
                        <el-table-column prop="kpName" label="平台">
                        </el-table-column>
                        <el-table-column prop="sdName" label="用能单位">
                        </el-table-column>
                        <el-table-column label="起止码时间">
                            <template slot-scope="scope">
                                <div>
                                    {{scope.row.arSt?formatDate(scope.row.arSt):''}}—{{scope.row.arEt?formatDate(scope.row.arEt):''}}
                                </div>
                            </template>>
                        </el-table-column>
                        <el-table-column label="操作" align="left" fixed="right" class-name="small-padding fixed-width"
                            width="100">
                            <template slot-scope="scope">
                                <div class="handBtns">
                                    <div class="handBtn" @click="handDetail(scope.row,scope.row.id)">查看</div>
                                    <div class="handBtn" @click="handPrint(scope.row)">打印</div>
                                </div>
                            </template>>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="crew-pagination">
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                        :current-page.sync="queryFrom.pageNum" :page-sizes="[10, 20, 50, 100]"
                        :page-size="queryFrom.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <CrewDialog :dialogVisible="dialogVisible" :dialogTable="dialogTable" :dialogBaseData="dialogBaseData"
            :status="queryFrom.status" :id="billId" @cancel="handleClose" @getList="getList">
        </CrewDialog>
        <el-dialog :title="printType==1?'电量确认单':'日用电明细单'" :visible.sync="open" width="65%" :before-close="cancle"
            top="80vh">
            <el-tabs v-model="printType">

                <el-tab-pane label="电量确认单" name="1">
                    <div class="print-body">
                        <TempalteBill v-if="printType==1" :type="1" :tableData="detailData"></TempalteBill>
                    </div>
                </el-tab-pane>
                <el-tab-pane v-if="detailData.kcCode==='KC0001'" label="日用电明细单" name="4">
                    <div class="print-body">
                        <TempalteBill v-if="printType==4" :type="4" :tableData="detailData"></TempalteBill>
                    </div>
                </el-tab-pane>


            </el-tabs>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="cancle">取消</el-button>
                <el-button type="primary" v-print="printObj">打 印</el-button>
            </span>

        </el-dialog>
    </div>
</template>

<script>
    import {
        getBills,
        billDetail
    } from '@/api/bill'
    import TempalteBill from "@/components/tempalteBill.vue"; //打印
    import {
        mapState
    } from 'vuex'
    import {
        formatDate
    } from '@/utils/index'
    import CrewDialog from './crewDialog.vue'
    import {
        Message
    } from 'element-ui'
    export default {
        name: 'crew',
        components: {
            CrewDialog,
            TempalteBill,
        },
        data() {
            return {
                dialogVisible: false,
                formatDate,
                tabs: ['待确认电量账单', '历史确认账单'],
                billId: null,
                queryFrom: {
                    pageNum: 1,
                    pageSize: 10,
                    status: 0,
                    userId: '',
                },
                total: 0,
                currentPage: 1,
                tableData: [],
                tableRows: [{
                        label: '平台',
                        kpName: 'kpName'
                    },
                    {
                        label: '用能单位',
                        kpName: 'sdName'
                    },
                    {
                        label: '起止码时间',
                        prop: '用能单位'
                    },
                    {
                        width: "100",
                        label: '操作',
                        prop: '',
                        fixed: true,
                    }
                ],
                dialogTable: [], //弹窗框表格信息
                dialogBaseData: {}, //弹窗框基本信息
                open: false,
                printType: '1',
                detailData: {},
                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                }
            }
        },
        created() {
            this.getBills()

        },
        computed: {
            ...mapState({
                userId: state => state.user.userId
            })
        },
        mounted() {

        },
        methods: {
            handleClose() {
                this.dialogVisible = false
            },
            cancle() {
                this.open = false
            },
            getBills() {
                this.queryFrom.userId = this.userId
                getBills(this.queryFrom).then(res => {
                    this.tableData = res.rows
                    this.total = res.total
                })
            },
            tabClick() {
                this.queryFrom.pageNum = 1;
                this.getBills();
            },
            handDetail(item, id) {
                this.dialogBaseData = item
                this.billId = id
                this.getDetail(id)

            },
            getList() {
                this.getBills();
            },
            getDetail(id) {
                billDetail(id).then(res => {
                    this.dialogTable = res.data
                    this.dialogVisible = true
                }).catch(err => {
                    Message()
                })
            },
            handleSizeChange(val) {
                this.queryFrom.pageSize = val
                // console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.queryFrom.pageNum = val
                // console.log(`当前页: ${val}`);
            },
            handPrint(item) {
                this.open = true;
                this.detailData = item;
                this.printType = '1';
            }
        },
        //keepalive 加载组件事件 再次加载数据
        activated: function () {
            this.getBills();
        },
        //keepalive 离开组件事件，清空数据
        deactivated: function () {
            this.tableData = [];
        }
    }
</script>

